/**
 * Validation Utilities
 * Contains validation functions for form fields
 * Used for both login and signup forms
 */

/**
 * Validates email format
 * @param email - Email string to validate
 * @returns Error message if invalid, empty string if valid
 */
export const validateEmail = (email: string): string => {
  if (!email) {
    return 'Email is required';
  }
  
  // Email regex pattern
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  
  if (!emailRegex.test(email)) {
    return 'Please enter a valid email address';
  }
  
  return '';
};

/**
 * Validates password for login
 * @param password - Password string to validate
 * @returns Error message if invalid, empty string if valid
 */
export const validatePassword = (password: string): string => {
  if (!password) {
    return 'Password is required';
  }
  
  if (password.length < 6) {
    return 'Password must be at least 6 characters long';
  }
  
  return '';
};

/**
 * Validates password for signup with stronger requirements
 * @param password - Password string to validate
 * @returns Error message if invalid, empty string if valid
 */
export const validateSignupPassword = (password: string): string => {
  if (!password) {
    return 'Password is required';
  }
  
  if (password.length < 8) {
    return 'Password must be at least 8 characters long';
  }
  
  // Check for at least one uppercase letter
  if (!/[A-Z]/.test(password)) {
    return 'Password must contain at least one uppercase letter';
  }
  
  // Check for at least one lowercase letter
  if (!/[a-z]/.test(password)) {
    return 'Password must contain at least one lowercase letter';
  }
  
  // Check for at least one number
  if (!/[0-9]/.test(password)) {
    return 'Password must contain at least one number';
  }
  
  return '';
};

/**
 * Validates name field
 * @param name - Name string to validate
 * @returns Error message if invalid, empty string if valid
 */
export const validateName = (name: string): string => {
  if (!name) {
    return 'Name is required';
  }
  
  if (name.trim().length < 2) {
    return 'Name must be at least 2 characters long';
  }
  
  if (name.trim().length > 50) {
    return 'Name must be less than 50 characters';
  }
  
  // Check if name contains only letters, spaces, and common name characters
  const nameRegex = /^[a-zA-Z\s'-]+$/;
  if (!nameRegex.test(name.trim())) {
    return 'Name can only contain letters, spaces, hyphens, and apostrophes';
  }
  
  return '';
};

/**
 * Validates confirm password field
 * @param password - Original password
 * @param confirmPassword - Confirmation password
 * @returns Error message if invalid, empty string if valid
 */
export const validateConfirmPassword = (
  password: string,
  confirmPassword: string
): string => {
  if (!confirmPassword) {
    return 'Please confirm your password';
  }
  
  if (password !== confirmPassword) {
    return 'Passwords do not match';
  }
  
  return '';
};

