'use client';

import { motion } from 'framer-motion';
import { Box } from '@mui/material';

/**
 * Floating Shapes Component
 * Creates animated floating shapes in the background with hover effects
 */
export default function FloatingShapes() {
  // Large gradient shapes
  const largeShapes = [
    {
      size: 200,
      initialX: '10%',
      initialY: '20%',
      duration: 20,
      delay: 0,
      gradient: 'linear-gradient(135deg, rgba(99, 102, 241, 0.4), rgba(139, 92, 246, 0.3))',
    },
    {
      size: 150,
      initialX: '80%',
      initialY: '60%',
      duration: 25,
      delay: 2,
      gradient: 'linear-gradient(135deg, rgba(139, 92, 246, 0.4), rgba(236, 72, 153, 0.3))',
    },
    {
      size: 180,
      initialX: '50%',
      initialY: '80%',
      duration: 30,
      delay: 4,
      gradient: 'linear-gradient(135deg, rgba(236, 72, 153, 0.4), rgba(99, 102, 241, 0.3))',
    },
    {
      size: 120,
      initialX: '20%',
      initialY: '50%',
      duration: 22,
      delay: 1,
      gradient: 'linear-gradient(135deg, rgba(99, 102, 241, 0.3), rgba(236, 72, 153, 0.4))',
    },
    {
      size: 100,
      initialX: '70%',
      initialY: '30%',
      duration: 28,
      delay: 3,
      gradient: 'linear-gradient(135deg, rgba(139, 92, 246, 0.3), rgba(99, 102, 241, 0.4))',
    },
  ];

  return (
    <Box
      sx={{
        position: 'fixed',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        overflow: 'hidden',
        pointerEvents: 'none',
        zIndex: 0,
      }}
    >
      {largeShapes.map((shape, index) => (
        <motion.div
          key={`large-${index}`}
          initial={{
            x: shape.initialX,
            y: shape.initialY,
            opacity: 0.2,
            scale: 0.8,
          }}
          animate={{
            y: [
              `${parseInt(shape.initialY)}%`,
              `${parseInt(shape.initialY) - 15}%`,
              `${parseInt(shape.initialY) + 15}%`,
              `${parseInt(shape.initialY)}%`,
            ],
            x: [
              `${parseInt(shape.initialX)}%`,
              `${parseInt(shape.initialX) + 8}%`,
              `${parseInt(shape.initialX) - 8}%`,
              `${parseInt(shape.initialX)}%`,
            ],
            rotate: [0, 180, 360],
            scale: [0.8, 1.1, 0.9, 0.8],
            opacity: [0.2, 0.35, 0.25, 0.2],
          }}
          transition={{
            duration: shape.duration,
            repeat: Infinity,
            ease: 'easeInOut',
            delay: shape.delay,
          }}
          whileHover={{
            scale: 1.3,
            opacity: 0.5,
            transition: { duration: 0.3 },
          }}
          style={{
            position: 'absolute',
            borderRadius: '50%',
            filter: 'blur(50px)',
            mixBlendMode: 'multiply',
            pointerEvents: 'auto',
            cursor: 'pointer',
          }}
        >
          <Box
            sx={{
              width: shape.size,
              height: shape.size,
              borderRadius: '50%',
              background: shape.gradient,
              position: 'relative',
            }}
          />
        </motion.div>
      ))}

      {/* Additional geometric shapes */}
      {[...Array(8)].map((_, index) => {
        const positions = [
          { x: '15%', y: '15%' },
          { x: '85%', y: '25%' },
          { x: '25%', y: '75%' },
          { x: '75%', y: '85%' },
          { x: '45%', y: '10%' },
          { x: '55%', y: '90%' },
          { x: '10%', y: '50%' },
          { x: '90%', y: '50%' },
        ];
        const pos = positions[index % positions.length];
        const size = 60 + (index % 3) * 20;
        const duration = 15 + (index % 5) * 3;

        return (
          <motion.div
            key={`shape-${index}`}
            initial={{
              x: pos.x,
              y: pos.y,
              opacity: 0.1,
              rotate: 0,
            }}
            animate={{
              y: [
                `${parseInt(pos.y)}%`,
                `${parseInt(pos.y) - 15}%`,
                `${parseInt(pos.y) + 15}%`,
                `${parseInt(pos.y)}%`,
              ],
              x: [
                `${parseInt(pos.x)}%`,
                `${parseInt(pos.x) + 8}%`,
                `${parseInt(pos.x) - 8}%`,
                `${parseInt(pos.x)}%`,
              ],
              rotate: [0, 180, 360],
              opacity: [0.1, 0.2, 0.15, 0.1],
            }}
            transition={{
              duration: duration,
              repeat: Infinity,
              ease: 'easeInOut',
              delay: index * 0.5,
            }}
            whileHover={{
              scale: 1.5,
              opacity: 0.3,
              rotate: 360,
              transition: { duration: 0.5 },
            }}
            style={{
              position: 'absolute',
              pointerEvents: 'auto',
            }}
          >
            <Box
              sx={{
                width: size,
                height: size,
                borderRadius: index % 2 === 0 ? '50%' : '20%',
                background: index % 3 === 0
                  ? 'linear-gradient(135deg, rgba(99, 102, 241, 0.2), rgba(139, 92, 246, 0.2))'
                  : index % 3 === 1
                  ? 'linear-gradient(135deg, rgba(139, 92, 246, 0.2), rgba(236, 72, 153, 0.2))'
                  : 'linear-gradient(135deg, rgba(236, 72, 153, 0.2), rgba(99, 102, 241, 0.2))',
                filter: 'blur(30px)',
                mixBlendMode: 'multiply',
              }}
            />
          </motion.div>
        );
      })}
    </Box>
  );
}

